<template>
  <div>
    <b-container >
      <b-carousel
        class="m-4"
        id="carousel-1"
        v-model="slide"
        :interval="4000"
        controls
        indicators
        background="#ababab"
        img-width="1024"
        img-height="480"
        style="text-shadow: 1px 1px 2px #333;"
        @sliding-start="onSlideStart"
        @sliding-end="onSlideEnd"
      >
        <!-- Text slides with image -->
        <b-carousel-slide
          caption="Control a distancia"
          text="Controla a tu arduino desde cualquier lado"
          img-src="https://i.ytimg.com/vi/0ElxYhbDQdM/maxresdefault.jpg"
        ></b-carousel-slide>

        <!-- Slides with custom text -->
        <b-carousel-slide img-src="https://static.makeuseof.com/wp-content/uploads/2019/05/arduino-robots-no-coding-670x335.jpg">
          <h1>Proyectos de todo tipo</h1>
        </b-carousel-slide>

        <!-- Slides with image only -->
        <b-carousel-slide
          img-src="https://static.techspot.com/images2/news/bigimage/2017/10/2017-10-16-image-10.jpg"
        ></b-carousel-slide>

        <!-- Slides with img slot -->
        <!-- Note the classes .d-block and .img-fluid to prevent browser default image alignment -->
        <b-carousel-slide>
          <template v-slot:img>
            <img
              class="d-block img-fluid w-100"
              width="1024"
              height="480"
              src="https://inteng-storage.s3.amazonaws.com/images/DECEMBER/sizes/robot_resize_md.jpg"
              alt="image slot"
            />
          </template>
        </b-carousel-slide>

        <!-- Slide with blank fluid image to maintain slide aspect ratio -->
     
      </b-carousel>
    </b-container>

   
  </div>
</template>

<script>
export default {
  name: "Carousel",
  data() {
    return {
      slide: 0,
      sliding: null
    };
  },
  methods: {
    onSlideStart(slide) {
      this.sliding = true;
    },
    onSlideEnd(slide) {
      this.sliding = false;
    }
  }
};
</script>

<style scoped>
  
</style>


