<template>
<div class="container py-5">
    <div class="row">
        <div class="col-md-12">
            <h2 class="text-center mb-5">Registrate, es gratis</h2>
            <div class="row">
                <div class="col-md-6 mx-auto">
                    <div class="card border-secondary">
                        <div class="card-header">
                            <h3 class="mb-0 my-2">Completa los siguientes campos</h3>
                        </div>
                        <div class="card-body">
                            <form class="form" role="form" autocomplete="off" v-on:submit="onSubmit" >
                                <div class="form-group">
                                    <label for="inputName">Nombre de usuario</label>
                                    <input type="text" v-model="form.username" class="form-control" id="inputName" placeholder="Nombre de usuario">
                                </div>
                                <div class="form-group">
                                    <label for="inputEmail3">Nombres</label>
                                    <input type="text" v-model="form.first_name" class="form-control" id="inputEmail3" placeholder="Nombres" required="">
                                </div>
                                <div class="form-group">
                                    <label for="inputPassword3">Apellidos</label>
                                    <input type="text" v-model="form.last_name" class="form-control" id="inputPassword3" placeholder="Apellidos" required="">
                                </div>
                                <div class="form-group">
                                    <label for="inputVerify3">Email</label>
                                    <input type="email" v-model="form.email"  class="form-control" id="inputVerify3" placeholder="correo" required="">
                                </div>
                                 <div class="form-group">
                                    <label for="inputVerify3">ContraseÃ±a</label>
                                    <input type="password" v-model="form.password"  class="form-control" id="inputVerify3" placeholder="password " required="">
                                </div>
                                <div class="form-group">
                                    <button type="submit" class="btn btn-success btn-lg float-right">Registrar</button>
                                </div>
                            </form>
                            <h3 v-if="confirm" class="text-success"  >Se registro correctamente, ya puede iniciar Sesion</h3>
                        </div>
                    </div>
                </div>
            </div>
            <!--/row-->

        </div>
        <!--/col-->
    </div>
    <!--/row-->
</div>
<!--/container-->
</template>

<script>
import { mapState } from "vuex";
import axios from "axios";


export default {
    name: 'Register',
    data(){
        return{
            form: {
                username: '',
                first_name: '',
                last_name: '',
                password: '',
                email: ''
            },
            confirm: false
          
        }

    },
    methods: {
            onSubmit(evt) {
      evt.preventDefault();
      const url = "http://ironspider.pythonanywhere.com/api/register/";
      const requestBody = {
        username: this.form.username,
        first_name: this.form.first_name,
        last_name: this.form.last_name,
        password: this.form.password,
        email: this.form.email
      };
      const config = {
        headers: {
          "content-type": "application/x-www-form-urlencoded"
        }
      };
      axios.post(url, requestBody).then(result=>{
          console.log(result.data)
          this.confirm = true
          
          
      }).catch(e => {
        if(e.response){
          this.error = true
          console.log("ejecutando error")
        }
      });
    },
    }
}
</script>


