import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/views/Index'
import Login from '@/views/Login'
import Menu from '@/views/Menu'
import Register from '@/views/Register'
import Inicio from '@/sections/Inicio.vue'
import Robots from '@/sections/Robots.vue'
import RobotDetail from '@/sections/RobotDetail.vue'
import Sensors from '@/sections/Sensors.vue'

import MyInfo from '@/sections/MyInfo.vue'


Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.VUE_APP_DOMINIO,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Index
    },
    {
      path: '/register',
      name: 'register',
      component: Register
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/menu',
   
      component: Menu,
      children: [
        { path: 'inicio', component: Inicio, name: 'inicio' },
        { path: 'robots', component: Robots, name: 'robots' },
        { path: 'robot/:id', component: RobotDetail, name: 'robotdetail'},
        { path: 'myinfo',  component: MyInfo,name: 'myinfo'},
        { path: 'sensors',  component: Sensors,name: 'sensors'},

        
      ],
    },
    
   
  ]
})



