<template>
<div>
    <div class='text-center' >
        <iframe width="450" height="260" style="border: 1px solid #cccccc;" src="https://thingspeak.com/channels/914404/charts/1?bgcolor=%23ffffff&color=%23d62020&dynamic=true&results=60&type=line&yaxismax=100&yaxismin=10"></iframe>
    </div>
    <button v-on:click='charge' class="btn btn-primary" >Cargar</button>

    <table class="table table-striped">
    <thead>
      <tr>
        <th>Fecha y hora</th>
        <th>Sensor</th>
        <th>Temperatura</th>
        <th>Humedad</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for='element in elements' :key="element.id" >
        <td>{{ element.date }}</td>
        <td>{{ element.sensorinstance }}</td>
        <td>{{ element.field1 }}</td>
         <td>{{ element.field2 }}</td>
      </tr>
    </tbody>
  </table>
</div>
</template>

<script>
import axios from "axios";
export default {
    name: 'Sensors',
    data(){
        return{
            elements: {}
        }
    },
    methods: {
            charge() {
      
      const url = "http://ironspider.pythonanywhere.com/api/reads/";
     
      const config = {
        headers: {
          "content-type": "application/x-www-form-urlencoded"
        }
      };
      axios.get(url).then(result=>{
          console.log(result.data)
          this.elements = result.data
          
          
          
      }).catch(e => {
        if(e.response){
        
          console.log("ejecutando error")
        }
      });
    },
    }
}

</script>


